.intro {
  padding-left: 2%;
}

.art-container {
  -ms-flex-positive: 1;
      flex-grow: 1;
  overflow-y: scroll; 
  width: 100px;  
  height: 92vh;
}

.art {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  overflow-y: scroll; 
}

.art img {
  width: 100%;
  height: auto;
}

/* Medium screens */
@media all and (max-width: 800px) {
  .art {
    -ms-flex-pack: distribute;
        justify-content: space-around;
  }
}

/* Small screens */
@media all and (max-width: 500px) {
  .art {
    -ms-flex-direction: column;
        flex-direction: column;
  }
}




