.intro {
  background-color: #333333;
  color: white;
  text-transform: uppercase;
  height: 5vh;
  line-height: 5vh;
}

.about-container {
  display: -ms-flexbox;
  display: flex;
  padding: 7vh;
  -ms-flex-pack: center;
      justify-content: center;
}

.about-container img {
  width:40vh; 
  height: auto;
  padding: 2vh;
  padding-right: 3rem;
}

.links {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-pack: center;
      justify-content: center
}

.links img {
  width: 10vh;
  -ms-flex-item-align: center;
      align-self: center;
  padding: 5rem;
}

@media all and (max-width: 800px) {
  .about-container {
    -ms-flex-direction: column;
        flex-direction: column;
    padding: 25px;
    text-align: center;
  }

  .links img {
    padding: .5rem;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    padding-bottom: 2rem;
  }
}

@media all and (max-width: 500px) {
  .about-container {
    -ms-flex-direction: column;
        flex-direction: column;
    text-align: center;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
  }
}






